.countryDetailAll {
  display: grid;
  grid-row: 5/7;
  grid-column: 2/6;
  grid-template-columns: repeat(3, 1fr);
  background-color: rgb(37, 37, 37);
  padding-bottom: 3%;
}

.countryDetailStyle {
  display: grid;
  grid-row: 1;
  grid-column: 3;
  text-align: left;
}
.countryName {
  grid-column: 1/2;
}

.position {
  font-size: x-small;
}

.countryChart {
  grid-column: 0/3;
  align-content: center;
}
.loader {
  text-align: center;
  margin: 100px auto;
  margin-left: 580px;
  font-size: 25px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load5 1.1s infinite ease;
  animation: load5 1.1s infinite ease;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
